import { PropsWithChildren, ReactNode } from "react"
import { Typography } from "@mui/material"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { UseQueryResult } from "@tanstack/react-query"
import { WatchlistDataTableDataStatusCell } from "./WatchlistDataTableDataStatusCell"

type Props = PropsWithChildren<{
  value?: unknown
  queryResult?: UseQueryResult<unknown, Error>
  tooltip?: ReactNode
}>

function WatchlistDataTableTextCell({ children, tooltip, value, queryResult }: Props) {
  return (
    <WatchlistDataTableDataStatusCell queryResult={queryResult} value={value}>
      <RPTooltip title={tooltip} placement="top">
        <Typography component={"span"}>{children}</Typography>
      </RPTooltip>
    </WatchlistDataTableDataStatusCell>
  )
}

export { WatchlistDataTableTextCell }
